.wp-block[data-type="adeleyeayodeji/dynamic-iframe-for-wp"] {
  &:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 10px;
    background: rgba(0, 0, 0, 0.15);
    pointer-events: all;
    cursor: pointer;
  }
  &.is-selected:before {
    display: none;
  }
}
